import { BaseNodeComponent } from './base-node/base-node.component';
import { Connection } from './../shared/connection.model';
import { ItemFilter } from './../services/item.service';
import { Component, Output, Input } from 'rete';
import { SocketController } from './sockets';
import { ResourceControl } from './controls/resource-control/resource-control';
import { AngularComponent } from 'rete-angular-render-plugin';

export class ResourceNode extends Component {

  constructor(private socketController: SocketController) {
    super('Resource');
  }

  builder(node) {
    var control = new ResourceControl(this.editor, 'resource', ItemFilter.NON_CRAFTABLE);
    node.addControl(control);
    node.data['type'] = 'output';
    
    return node;
  }

  worker(node, inputs, outputs) {
    var node_ref = this.editor.nodes.find(n => n.id === node.id);

    if (node.data.changed === true)
    {
      node_ref.getConnections().forEach(element => {
        this.editor.removeConnection(element);
      });
      
      node_ref.outputs.clear();
      var item = node.data.resource;

      var output = new Output("resource", item.name, this.socketController.GetSocketForId(item.id));
      node_ref.addOutput(output);
      
      this.editor.trigger('nodeselected');

      node.data.changed = false;
    }

    let datagram = new Connection(node.data.resource, node.data.amount, node.data.perHour);
    outputs['resource'] = datagram;
  }
}