import { ItemImageComponent } from './../../../shared/item-image-component/item-image.component';
import { ItemFilter, ItemService } from './../../../services/item.service';
import { Item } from './../../../shared/item.model';

import { Component, Input, Type, ViewChild } from '@angular/core';
import { Control, Node, NodeEditor } from 'rete';
import { AngularControl } from 'rete-angular-render-plugin';

@Component({
  templateUrl: "./resource-control.html",
  styleUrls: ["./resource-control.sass",]
})
export class ResourceComponent {
  @Input() craftable!: ItemFilter;
  @Input() readonly!: boolean;
  @Input() mounted!: Function;
  @Input() changeItem!: Function;
  @Input() changeRate!: Function;
  @Input() click!: Function;
  @Input() mouseDown!: Function;
  @Input() mouseUp!: Function;
  
  @ViewChild(ItemImageComponent) itemImage: ItemImageComponent;
  
  public selectedItem!: Item;
  public perHour!: number;
  public perHourString: string;
  public background: any;
  
  constructor(private itemService: ItemService) {
    this.background = {};
  }
  
  ngOnInit() {
    this.mounted();
  }

  onChangeRate() {
    let num = parseFloat(this.perHourString);

    this.perHour = num;
    this.changeRate(this.perHour);
  }
}

export class ResourceControl extends Control implements AngularControl {
  component: Type<ResourceComponent>
  props: { [key: string]: unknown }
  
  constructor(public emitter: NodeEditor, public key, craftable: ItemFilter) {
    super(key); 
  
    this.component = ResourceComponent;
    this.props = {
      selectedItem: Item,
      craftable: craftable,
      readonly: false,
      perHour: 1,
      perHourString: "1",
      changeRate: v => this.onChangeRate(v),
      mounted: () => {
        this.setInitialItem();
      },
      click: () => this.onClick(),
      mouseDown: () => this.onMouseDown(),
      mouseUp: () => this.onMouseUp(),
    }
  }

  onChangeRate(rate: number) {
    this.setRate(rate);

    this.emitter.trigger('process');
  }

  setRate(rate: number) {
    this.props.perHour = rate;

    let str = rate == Infinity ? "-" : rate.toString();
    if (str.indexOf('.') !== -1) {
      str = str.slice(0, str.indexOf('.') + 3);
    }
    this.props.perHourString = str;

    this.putData('perHour', rate);
  }

  private setInitialItem() {
    let item = this.getData('resource') as Item;
    this.props.selectedItem = item;
    this.putData(this.key, item);
    this.putData('changed', true);

    this.putData('amount', item.craftable ? item.createdAmount : 1)
    this.putData('perHour', this.props.perHour);
  }

  onClick() {
    this.emitter.selected.clear();
    this.emitter.nodes.map(n => n.update());
  }

  onMouseDown() {
    this.emitter.selected.clear();
    this.emitter.nodes.map(n => n.update());
  }

  onMouseUp() {
    this.emitter.selected.clear();
    this.emitter.nodes.map(n => n.update());
  }
}