import { Item } from '../shared/item.model';
import { Injectable } from '@angular/core';

export enum ItemFilter
{
  CRAFTABLE,
  NON_CRAFTABLE,
  ANY,
};

@Injectable({
  providedIn: 'root'
})
export abstract class ItemService {
  constructor() {
  }

  public abstract GetAllItems(): Item[];
  public abstract GetItemFromId(id: number): Item;
  public abstract GetAllCraftableItems(): Item[];
  public abstract GetAllNonCraftableItems(): Item[];
}
