import { BaseNodeComponent } from './base-node/base-node.component';
import { Connection } from './../shared/connection.model';
import { SocketController } from './sockets';
import { ItemService } from 'src/app/rete-modules/services/item.service';
import { ItemFilter } from './../services/item.service';
import { Component, Output, Input, Socket } from 'rete';
import { ResourceControl } from './controls/resource-control/resource-control';

export class CrafterNode extends Component {

  constructor(private socketController: SocketController, private itemService: ItemService) {
    super('Crafter');
  }

  builder(node) {
    var control = new ResourceControl(this.editor, 'resource', ItemFilter.CRAFTABLE
    );
    node.addControl(control);

    return node;
  }

  worker(node, inputs, outputs) {

    var node_ref = this.editor.nodes.find(n => n.id === node.id);

    if (node.data.changed === true)
    {
      node_ref.getConnections().forEach(element => {
        this.editor.removeConnection(element);
      });
      
      node_ref.outputs.clear();
      var item = node.data.resource;
      var output = new Output("resource", item.name  + " x " + item.createdAmount, this.socketController.GetSocketForId(item.id));
      node_ref.addOutput(output);

      node_ref.inputs.clear();
      item.recipe.forEach(element => {
        var item = this.itemService.GetItemFromId(element.id);
        var input = new Input("" + item.id, item.name + " x " + element.amount , this.socketController.GetSocketForId(item.id));
        node_ref.addInput(input);
      });

      this.editor.trigger('nodeselected');

      node.data.changed = false;
    }

    var perHourBottleneck = Infinity;
    var craftedItem = node.data.resource;
    for (var recipeIndex in craftedItem.recipe) {
      let item = craftedItem.recipe[recipeIndex];
      let input = inputs[item.id];
      console.log(item);
      console.log(input);

      // Input found
      if (input && input[0]) {
        let itemBottleneck = input[0].amount / item.amount;
        itemBottleneck *= input[0].perHour;

        if (itemBottleneck < perHourBottleneck) {
          console.log("Smaller")
          perHourBottleneck = itemBottleneck;
        }
      }
    }

    let control = <ResourceControl>node_ref.controls.get('resource');
    control.props.readonly = true;
    control.setRate(perHourBottleneck);
      
    var datagram = new Connection(node.data.resource, node.data.amount, node.data.perHour);
    outputs['resource'] = datagram;
  }
}