import { HighlightSearchPipe } from './../shared/highlight-text.pipe';
import { Item } from './../shared/item.model';
import { ItemService } from 'src/app/rete-modules/services/item.service';
import { Component, OnInit } from '@angular/core';

class TempNode {
  public type: String;
  public name: String;

  constructor(type: String, name: String) {
    this.type = type;
    this.name = name;
  }
};

class NodeBrowserItem extends Item {
  public htmlName: string;
};

@Component({
  selector: 'app-node-browser',
  templateUrl: './node-browser.component.html',
  styleUrls: ['./node-browser.component.sass']
})
export class NodeBrowserComponent implements OnInit {
  public nodeList: TempNode[];
  public itemList: NodeBrowserItem[];

  public filter: number = 0;
  public searchString: string = "";

  // @ViewChildren('browseritems') itemsList: QueryList<ItemImageComponent>;
  // private items: ItemImageComponent[];


  constructor(private itemService: ItemService) { 
    this.nodeList = [];
    this.itemList = itemService.GetAllItems() as NodeBrowserItem[];

    this.itemList.forEach(value => {
      value.htmlName = value.name;
    })
  }

  ngOnInit(): void {
  }
  
  onDrag(event: any, item: Item) {
    let nodeType = "";
    if (this.filter == 0) {
      nodeType = item.craftable ? "Crafter" : "Resource";
    }
    else if (this.filter == 1) {
      nodeType = "Resource";
    }
    else if (this.filter == 2) {
      nodeType = "Crafter"
    }
    else if (this.filter == 3) {
      nodeType = "Output"
    }

    event.dataTransfer.setData("nodeType", nodeType)

    event.dataTransfer.setData("nodeId", item.id);
    event.dataTransfer.effectAllowed = "copy";
  }

  onAllClicked() {
    this.filter = 0;
    this.filterItems();
  }

  onResourcesClicked() {
    this.filter = 1;
    this.filterItems();
  }

  onCraftableClicked() {
    this.filter = 2;
    this.filterItems();
  }

  onOutputClicked() {
    this.filter = 3;
    this.filterItems();
  }

  onSearchChange() {
    this.searchString = this.searchString.toLowerCase();
    this.filterItems();
  }

  private filterItems() {
    if (this.filter == 0 || this.filter == 3) {
      this.itemList = this.itemService.GetAllItems() as NodeBrowserItem[];
    }

    if (this.filter == 1) {
      this.itemList = this.itemService.GetAllNonCraftableItems() as NodeBrowserItem[];
    }

    if (this.filter == 2) {
      this.itemList = this.itemService.GetAllCraftableItems() as NodeBrowserItem[];
    }

      this.itemList = this.itemList.filter(value => {
        let index = value.name.toLowerCase().indexOf(this.searchString);
        if (index >= 0) {
          value.htmlName = value.name.substring(0, index)
            + "<mark>"
            + value.name.substring(index, index + this.searchString.length)
            + "</mark>"
            + value.name.substring(index + this.searchString.length, value.name.length);
        }
        else {
          value.htmlName = value.name;
        }

        return index >= 0;
      })
  }
}
