import { Item } from './../shared/item.model';
import { BaseNodeComponent } from './../nodes/base-node/base-node.component';
import { NodeTree } from './../shared/node-tree';
import { OutputNode } from './../nodes/output-node.component';
import { ItemService } from './../services/item.service';
import { CrafterNode } from './../nodes/crafter-node.component';
import { SocketController } from './../nodes/sockets';
import { Component, AfterViewInit, ViewChild, ElementRef } from '@angular/core';

import { Component as ReteComponent, NodeEditor, Engine } from 'rete';
import ConnectionPlugin from 'rete-connection-plugin';

import { ResourceNode } from './../nodes/resource-node.component';
import { AngularRenderPlugin, ElementProps } from 'rete-angular-render-plugin';

@Component({
  selector: 'app-grid',
  templateUrl: './grid.component.html',
  styleUrls: ['./grid.component.sass']
})
export class GridComponent implements AfterViewInit {
  
  @ViewChild('nodeEditor', { static: true }) private el: ElementRef;
  private editor = null;
  private engine: Engine;
  private components: { [type: string]: ReteComponent };

  private nodeTree: NodeTree;

  constructor(private socketController: SocketController, private itemService: ItemService) { 
  }

  async ngAfterViewInit() {
    const container = this.el.nativeElement;
    
    this.components = {};

    this.components['Resource'] = new ResourceNode(this.socketController);
    this.components['Crafter'] = new CrafterNode(this.socketController, this.itemService);
    this.components['Output'] = new OutputNode(this.socketController);

    this.editor = new NodeEditor('minecraft-web-app@0.1.0', container);
    this.editor.use(ConnectionPlugin);
    this.editor.use(AngularRenderPlugin, {component: BaseNodeComponent});

    this.engine = new Engine('minecraft-web-app@0.1.0');

    this.nodeTree = new NodeTree();

    for (const componentKey in this.components) {
      let component = this.components[componentKey];

      this.editor.register(component);
      this.engine.register(component);
    }

    this.createNode("Resource", 1, 100, 100);
    this.createNode("Crafter", 5, 400, 100);
    this.createNode("Output", 340, 700, 100);

    this.editor.on(['nodecreated', 'noderemoved', 'connectionremoved'], (async () => {
      await this.engine.abort();
      await this.engine.process(this.editor.toJSON());
    }) as any);

    this.editor.on(['process', 'connectioncreated'], async () => {
      await this.engine.abort();
      await this.engine.process(this.editor.toJSON());
      
      this.nodeTree.parseFromJson(this.editor.toJSON());
      this.updateCriticalPath();
    });

    this.editor.on('click', () => {
      this.editor.selected.clear();
      this.editor.nodes.map(n => n.update());
    })

    this.engine.on('error', (message) => {
      console.log(message);
    });
    
    this.editor.view.resize();
    this.editor.trigger('process');
  }

  updateCriticalPath() {
    let criticalPath = this.nodeTree.getCriticalPath();

    this.editor.nodes.forEach(value => {
      value.data.criticalPath = false;
    })

    criticalPath.forEach((value: any, index, array) => {
      let node_ref = this.editor.nodes.find(n => n.id === value.id);
      node_ref.data.criticalPath = true;
    });
  }
  
  dragOver(event) {
    event.preventDefault();

    event.dataTransfer.dropEffect = "copy";
  }

  onDrop(event) {
    event.preventDefault();

    const type = event.dataTransfer.getData("nodeType");
    const id = event.dataTransfer.getData("nodeId") as number;
    this.createNode(type, id, event.clientX, event.clientY);
  }

  private async createNode(nodeType: string, nodeId: number, x: number, y: number) {
    if (this.components.hasOwnProperty(nodeType)) {
      let component = await this.components[nodeType].createNode({ resource: this.itemService.GetItemFromId(nodeId) });
      
      let transform = this.editor.view.area.transform;
      let position = [(x - transform.x - 75) / transform.k, (y - transform.y - 125) / transform.k] as [number, number];

      component.position = position;
      this.editor.addNode(component);
    }
  }
}
