import { RecipeComponent } from './recipe.model';

export class Item {
  public id!: number;
  public name!: string;
  public index: any;
  public craftable: boolean;
  public createdAmount?: number;
  public recipe?: RecipeComponent[];

  constructor(id: number, name: string) {
    this.id = id;
    this.name = name;
  }
}